import React from "react";
import "./Footer.css";
import { BsFacebook, BsPinterest, BsTwitter } from "react-icons/bs";
import { IconContext } from "react-icons";
export const Footer = () => {
  return (
    <div className="footer">
      <div className="footerSection">
        <h4 className="footerLogo">
          <img
            src="data:image/webp;base64,"
            alt=""
          />
          TRAVALFLAP
        </h4>

        <p>
          Lorem ipsum dolor sit amet, consectetur adipisicing elit. Repudiandae
          quia iste nulla culpa commodi necessitatibus debitis? Veritatis, vitae
          praesentium! Explicabo . Lorem ipsum dolor, sit amet consectetur
          adipisicing elit. Deleniti ducimus cumque at sunt veniam molestiae
          facere non, dolore amet aliquid!
        </p>

        <IconContext.Provider value={{ className: "socialIcons" }}>
          <div className="socialmedia">
            <BsPinterest />
            <BsFacebook />
            <BsTwitter />
            <BsTwitter />
            <BsTwitter />
          </div>
        </IconContext.Provider>
      </div>
      <div className="footerSection">
        <h4>BLOG POSTS</h4>
        <div className="blogs">
          <div className="blogImg">
            <img
              src="https://preview.colorlib.com/theme/travelix/images/xfooter_blog_1.jpg.pagespeed.ic.FLhgS0NlTD.webp"
              alt=""
            />
          </div>
          <div className="blogText">
            <p>Travel with us this year</p>
            <p>Nov 29, 2020</p>
          </div>
        </div>
        <div className="blogs">
          <div className="blogImg">
            <img
              src="https://preview.colorlib.com/theme/travelix/images/xfooter_blog_1.jpg.pagespeed.ic.FLhgS0NlTD.webp"
              alt=""
            />
          </div>
          <div className="blogText">
            <p>Travel with us this year</p>
            <p>Nov 29, 2020</p>
          </div>
        </div>
        <div className="blogs">
          <div className="blogImg">
            <img
              src="https://preview.colorlib.com/theme/travelix/images/xfooter_blog_1.jpg.pagespeed.ic.FLhgS0NlTD.webp"
              alt=""
            />
          </div>
          <div className="blogText">
            <p>Travel with us this year</p>
            <p>Nov 29, 2020</p>
          </div>
        </div>
      </div>
      <div className="footerSection">
        <h4>TAGS</h4>
        <div className="tags">
          <button>design</button>
          <button>fashion</button>
          <button>music</button>
          <button>video</button>
          <button>party</button>
          <button>photography</button>
          <button>adventure</button>
          <button>travel</button>
        </div>
      </div>
      <div className="footerSection">
        <h4>CONTACT INFO</h4>

        <div className="footerContect">
          <img
            src="https://preview.colorlib.com/theme/travelix/images/placeholder.svg"
            alt=""
          />

            <p>4127 Raoul Wallenber 45b-c Gibraltar</p>
        </div>

        <div className="footerContect">
            <img
              src="https://preview.colorlib.com/theme/travelix/images/placeholder.svg"
              alt=""
            />
            <p>2556-808-8613</p>
        </div>
        <div className="footerContect">
            <img
              src="https://preview.colorlib.com/theme/travelix/images/placeholder.svg"
              alt=""
            />
            <p>contectme@gmail.com</p>
        </div>
        <div className="footerContect">
            <img
              src="https://preview.colorlib.com/theme/travelix/images/placeholder.svg"
              alt=""
            />
            <p>wwww.travelflap.net</p>
        </div>
      </div>
    </div>
  );
};

